* {
  box-sizing: border-box;
  background-color: #edeff2;
}

@font-face {
  font-family: "Helvetica";
  src: url('../fonts/Helvetica.ttf');
}

.logo-right{
  display: block;
  text-align: right;
}

.login-logo{
  height: 20vh;
  padding-top: 10vh;
  padding-left: 3vh;
  background-color: #ffffff;
}

.login{
  height: 30vh;
}

.window-color{
  background-color: #ffffff;
}

.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.left{
  display: block;
  text-align: left;
}

.title {
  font-family: 'Helvetica';
  font-size:1.1em;
  font-weight: bolder;
  color: 0;
  text-align: center;
  background-color: #ffffff;
  padding-left: 2vw;
}

.subtitle {
  font-family: 'Helvetica';
  font-size:0.9em;
  color: 0;
  text-align: center;
  background-color: #ffffff;
  padding-top: 0.5vh;
  padding-left: 2vw;
}

.logintitle {
  font-family: 'Helvetica';
  font-size:1.1em;
  font-weight: bolder;
  color: 0;
  text-align: center;
  background-color: #ffffff;
  padding-top: 12vh;
}

.loginsubtitle {
  font-family: 'Helvetica';
  font-size:0.9em;
  color: 0;
  text-align: center;
  background-color: #ffffff;
  padding-top: 1vh;

}


.logintab{
  padding: 2vh;
}

.white{
  background-color: white;
}

.signin{
  font-size:1.2em;
  color: #000000;
  background-color: #ffffff;
  display: block;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  border-radius: 25px;
  border-color: #959595;
  border-style: solid;
  margin-top: 5%;
  height: auto;
  bottom: 10%;
  font-family: "Helvetica";

}
.pwdbtn{
  font-size:1.2em;
  color: #ffffff;
  background-color: #959595;
  display: block;
  width: 60%;
  text-align: center;
  margin-left: 20%;
  margin-top: 5%;
  height: auto;
  bottom: 10%;
  font-family: "Helvetica";
}
.text{
  background-color: #edeff2;
  width: 60%;
  margin-top: 6vh;
  margin-left: 20%;
  border: 0px;
  border-radius: 5px;
  color: #000000;
  font-size: 1.1em;
  font-family: "Helvetica";
}

.loginform{
  padding-top: 2vh;
  background-color: #ffffff;
}

.reset{
  color: #000000;
  background-color: #ffffff;
  text-align:center;
  font-family: "Helvetica";
  font-style: italic;
}

.link{
  display: block;
  text-align: center;
  padding-top: 5vh;
}

label{
  color: #959595;
  background-color: #ffffff;
  font-family: "Helvetica";
  margin-left: 20%;
}

.pwdreset{
  height: 75vh;
  background-color: #ffffff;
  margin: 4vh;
}
.pwdtext{
  font-family: "Helvetica";
  font-size: 1.2em;
  padding-top: 2vh;
}

.pwdmessage{
  height: 15vh;
  font-family: "Helvetica";
  font-size: 1.1em;
  padding-top: 7vh;
}

@media (min-width: 768px){
  .login-logo{
    height: 13vh;
    padding-top: 0vh;
    padding-left: 1vh;
    background-color: #ffffff;
    height: 13vh;
    background-color: #ffffff;
    margin: 10vh;
  }

  .logintitle {
    font-family: 'Helvetica';
    font-size:1.4em;
    font-weight: bolder;
    color: 0;
    text-align: center;
    background-color: #ffffff;
    padding-top: 12vh;
  }

  .loginsubtitle {
    font-family: 'Helvetica';
    font-size:1.1em;
    color: 0;
    text-align: center;
    background-color: #ffffff;
    padding-top: 1vh;

  }
}
