
.display-stats{
  background-color: #ede2d5;
  height: 72vh;
  display: block;
  text-align: center;
  padding-top: 3vh;
  overflow-y: auto;
  overflow-x: hidden;
  padding-top: 12vh;
}

.next-button{
  font-size:1em;
  color: #959595;
  background-color: #ffffff;
  display: block;
  margin-left: 60%;
  width: 30%;
  height: 6vh;
  position: relative;
  font-family: "Helvetica";
  font-weight: bold;
  margin-bottom: 0;
  border-radius: 25px;
  border-color: #959595;
  border-style: solid;
  border-width: 2px;
}

.display-btn{
  display: block;
  text-align: center;
  background-color: #ede2d5;
  margin-top: 1vh;
}

.btn-stats:hover{
  background-color: #959595;
}

table {
  border-collapse: collapse;
  border: 1px solid #ccc;
  background-color: #ffffff;
}


th, td{
  border: 1px solid #787774;
  text-align: center;
  font-size: 0.8em;
}

th.user{
  color: blue;
  min-width: 200px;
}

th.data{
  min-width: 100px;
}

.table-topics{
  overflow-x: scroll;
}

.btn-stats{
  margin: 2vh;
  background-color: #ffffff;
  width: 80%;
  align-items: center;
  color: #959595;
  font-family: "Helvetica";
  font-size: 1em;
  border-radius: 25px;
  border-style: solid;
  border-width: 2px;
  border-color: #959595;
}

.stats-data{
  display: block;
  text-align: center;
  background-color: #ede2d5 !important;
}

.progress-bar{
  width: 100%;
}

.progress {
    color: white;
    width:auto;
    margin-top: 0.5vh;
    text-align: right;
  }

  .statsBar{
    width: 80%;
    margin-left: 10%;
    background-color: #ede2d5;
  }

.lightgreen{
  background-color: #ede2d5 !important;
}

.data-label{
  display: block;
  text-align: left;
  background-color: #ffffff;
  padding-left: 1vh;
  font-family: "Helvetica";
}

.display-data{
  background-color: #ede2d5;
  height: 75vh;
  overflow-x:auto;
}

.fixed{
  position: absolute;
  width: 10em;
  left: 0;
  top: auto;
  min-width: 100px;
  min-height: 30px;
}

thead th {
  position: -webkit-sticky; /* for Safari */
  position: sticky;
  top: 0;
  z-index: 1;
  border: 1px solid #787774;
  box-shadow: 0 0 0 1px #787774;
}

tbody th {
  position: -webkit-sticky; /* for Safari */
  position: sticky;
  left: 0;
  border: 1px solid #787774;
  box-shadow: 0 0 0 1px #787774;
}

thead th:first-child {
  left: 0;
  z-index: 2;

}

.rowheader{
  text-align: left;
  padding-left: 1vw;
}

.wordcloud{
  background-color: #ede2d5;
  padding-left: 2vw;
  padding-right: 2vw;

}

.session-btn-div{
  height: 100%;
  background-color: #ede2d5;
}

.clouddiv{
  background-color: #ede2d5;
  width: 90%;
  margin-left: 5%;
}

.topic-select{
  color: black;
}

.view-select{
  color: black;
}

.fullwidth{
  width: 100%;
}

.testlink{
  background-color: #ede2d5 !important;
  color: #000000;
  padding-top: 2vh;
}

