@font-face {
  font-family: "Helvetica";
  src: url('../fonts/Helvetica.ttf');
}

.fblabel{
  font-size: 1.1em;
  font-family: "Helvetica";
  color: #000000;
  padding: 2vh;
  margin-left: 3vh;
  margin-right: 3vh;
  background-color: #ede2d5 !important;
}

.fbtlabel{
  font-size: 1.5em;
  font-family: "Helvetica";
  color: #000000;
  padding: 2vh;
  margin-left: 3vh;
  margin-right: 3vh;
  background-color: #ede2d5 !important;
}
.fbarea{
  background-color: #ede2d5 !important;
  height: 85vh;
  overflow-y: auto;
}

.blank{
  margin-top: 5vh;
}

.white{
  background-color: #ffffff;
}

.fbtext{
  margin-left: 10%;
  width: 80%;
  height: 30vh;
  background-color: #ffffff;
  font-family: "Helvetica";
}

.fbsubmitbtn{
  font-size:1.2em;
  color: #959595;
  background-color: #ffffff;
  display: block;
  text-align: center;
  display: block;
  position: relative;
  height: auto;
  margin-top: 15%;
  margin-bottom: 0;
  font-family: "Helvetica";
  border-radius: 25px;
  border-color: #959595;
  border-style: solid;
  margin-left: auto;
  margin-right: auto;
}
